@import url(http://at.alicdn.com/t/font_1509203_amsjz2cbmyn.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: #eee;
}

.wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.wrap .card-wrap {
  width: 46vw;
  display: flex;
  justify-content: center;
  padding-top: 10px;
}

.wrap .card-wrap .img {
  width: 100%;
  height: 100%;
}

.wrap .card-wrap .text {
  text-align: center;
}

.link {
  position: absolute;
  right: 0;
}

.icon-github {
  color: #000;
}

.con {
  display: block;
  width: 100%;
  height: 100%;
  position: relative;
}
